* {
  margin: 0;
  box-sizing: border-box;
  font-size: 67.5%;
  font-family: "Averia Libre", cursive;
}

.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(to top left, rgb(185, 167, 4), #ffff6c);
  padding: 4rem;
}

.title-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  font-size: 8rem;
  font-family: "Cabin Sketch", cursive;
}

.bulb-img {
  height: 7rem;
  margin-left: 2rem;
  margin-right: 2rem;
  opacity: 0.8;
  border: 3px inset rgb(2, 174, 253);
  border-radius: 6rem;
}

.desc-wrapper {
  margin-top: 4rem;
  font-size: 3rem;
}

.interaction {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.input-wrapper {
  height: 10rem;
}

.input {
  height: 6rem;
  width: 42rem;
  border-radius: 0.5rem;
  outline: none;
  border: 1px solid rgb(221, 192, 29);
  font-size: 3rem;
  font-family: "Segoe UI";
  padding: 1.5rem;
}

.submit-btn {
  height: 6rem;
  width: 12rem;
  margin-left: 0.5rem;
  border-radius: 0.5rem;
  outline: none;
  border: none;
  box-shadow: 0.4rem 0.4rem 1rem rgba(0, 0, 0, 0.2);
  font-size: 1.5rem;
  background: linear-gradient(
    to top left,
    rgb(206, 206, 206),
    rgb(238, 238, 238)
  );
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.5s ease;
}

.submit-btn:hover {
  background: linear-gradient(
    to top left,
    rgb(196, 195, 195),
    rgb(219, 219, 219)
  );
}

.submit-btn:active {
  transform: translateY(5%);
}

.loading-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 6rem;
}

.loading-msg {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.loader-icon {
  height: 5rem;
  width: 5rem;
  animation: rotate 5s linear infinite;
}

@keyframes rotate {
  100% {
    transform: rotateZ(720deg);
  }
}

.idea-wrapper {
  font-size: 4rem;
  width: 85rem;
  margin: 1rem auto 2rem auto;
  padding: 3rem;
  background: linear-gradient(to top left, #ddd, #eee);
  border-radius: 1rem;
  box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 1100px) {
  .title {
    font-size: 6rem;
  }

  .desc-wrapper {
    font-size: 2.5rem;
  }

  .bulb-img {
    height: 6rem;
    width: 6rem;
  }

  .idea-wrapper {
    width: 70rem;
    font-size: 3rem;
  }
}

@media screen and (max-width: 850px) {
  .desc-wrapper {
    font-size: 2rem;
  }

  .idea-wrapper {
    width: 80vw;
  }
}

@media screen and (max-width: 750px) {
  .title {
    font-size: 4rem;
  }

  .bulb-img {
    height: 4rem;
    width: 4rem;
  }

  .input {
    font-size: 2.5rem;
    width: 60vw;
  }

  .submit-btn {
    width: 20vw;
  }
}

@media screen and (max-width: 500px) {
  .App {
    padding: 8vw;
  }

  .title {
    font-size: 3rem;
  }

  .bulb-img {
    height: 3rem;
    width: 3rem;
    margin-right: 1rem;
    margin-left: 0;
  }

  .interaction {
    flex-direction: column;
    align-items: center;
  }

  .input-wrapper {
    height: min-content;
    margin-bottom: 0.5rem;
  }

  .input {
    width: 80vw;
  }

  .submit-btn {
    width: 80vw;
    height: 5rem;
    position: relative;
    left: -0.3rem;
  }

  .idea-wrapper {
    margin-left: 0;
    margin-right: 0;
  }
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 2.5rem;
  }

  .input {
    font-size: 1.9rem;
  }
}

@media screen and (max-width: 300px) {
  .title {
    font-size: 2.2rem;
  }
}
